import { ref, computed, onMounted } from "vue";

export const collapsed = ref(false);
export const toggleSidebar = () => (collapsed.value = !collapsed.value);

export const SIDEBAR_WIDTH = 180;
export const SIDEBAR_WIDTH_COLLAPSED = 38;
export const sidebarWidth = computed(() => {
  const isMobile = window.innerWidth <= 768 && window.innerHeight <= 768;
  if (isMobile) {
    return `${SIDEBAR_WIDTH_COLLAPSED}px`; // Always return collapsed width on mobile
  } else {
    return `${collapsed.value ? SIDEBAR_WIDTH_COLLAPSED : SIDEBAR_WIDTH}px`;
  }
});

onMounted(() => {
  window.addEventListener("resize", updateSidebarWidth);
});

const updateSidebarWidth = () => {
  if (window.innerWidth <= 768) {
    collapsed.value = true; // Collapse sidebar on small screens
  } else {
    collapsed.value = false; // Expand sidebar on larger screens
  }
};
