<template>
  <div class="language-selector">
    <select @change="switchLanguage" class="selector">
      <option
        class="selector-option"
        v-for="sLocale in supportedLocales"
        :key="`locale-${sLocale}`"
        :value="sLocale"
        :selected="locale === sLocale"
      >
        {{ $t(`locale.${sLocale}`) }}
      </option>
    </select>
  </div>
</template>

<script>
import { useI18n } from "vue-i18n";
import Trans from "@/Translations";
export default {
  setup() {
    const { t, locale } = useI18n();
    const switchLanguage = async (event) => {
      const newLocale = event.target.value;
      await Trans.switchLanguage(newLocale);
    };
    const supportedLocales = Trans.supportedLocales;
    return { t, locale, supportedLocales, switchLanguage };
  },
};
/*{{ $t(`locale.${sLocale}`) }}*/
</script>

<style>
@media (max-width: 768px) {
  .selector {
    padding: 0.5em 1em; /* Adjust padding for mobile */
    width: 200px;
  }
  .language-selector {
    max-width: 100%; /* Ensure the selector does not overflow */
    width: 500px;
  }
}
.selector {
  align-self: center;
  appearance: none;
  /* safari */
  -webkit-appearance: none;
  /* other styles for aesthetics */
  width: 100%;
  padding: 0.675em 1em 0.675em 1em;
  /*background-color: #232323;*/
  background: none;
  border-radius: 1rem;
  color: #ffffff;
  cursor: pointer;
}
.selector-option {
  background-color: #232323;
}
.language-selector {
  align-self: center;
  width: 100%;
  max-width: 100%; /* Adjust as needed */
  padding: 1em 1em; /* Adjust padding as needed */
}
</style>
