<template>
  <div class="home">
    <img :src="imagePath" class="hero-image" />
  </div>
</template>

<script>
export default {
  name: "HomeView",
  computed: {
    imagePath() {
      const imagePaths = {
        en: require("@/assets/images/home_en.jpg"),
        ja: require("@/assets/images/home_ja.jpg"),
      };

      return imagePaths[this.$i18n.locale];
    },
  },
};
</script>

