<template>
  <div class="sidebar" :style="{ width: sidebarWidth }">
    <h1>
      <span v-if="collapsed">
        <img src="../../assets/logo.png" class="logo-collapsed" />
        <p style="font-size: small">GHB</p>
      </span>
      <span v-else><img src="../../assets/logo.png" class="logo" /></span>
    </h1>

    <SidebarLink :to="Trans.i18nRoute({ name: 'home' })" icon="fas fa-home">{{
      $t("sidebar.home")
    }}</SidebarLink>
    <SidebarLink
      :to="Trans.i18nRoute({ name: 'aboutme' })"
      icon="fa-solid fa-person-rays"
      >{{ $t("sidebar.aboutme") }}</SidebarLink
    >
    <SidebarLink
      :to="Trans.i18nRoute({ name: 'programming' })"
      icon="fa-solid fa-computer"
      >{{ $t("sidebar.programming") }}</SidebarLink
    >
    <SidebarLink
      :to="Trans.i18nRoute({ name: 'art' })"
      icon="fa-solid fa-palette"
      >{{ $t("sidebar.art") }}</SidebarLink
    >
    <SidebarLink
      :to="Trans.i18nRoute({ name: 'music' })"
      icon="fa-solid fa-compact-disc"
      >{{ $t("sidebar.music") }}</SidebarLink
    >
    <SidebarLink
      :to="Trans.i18nRoute({ name: 'travels' })"
      icon="fa-solid fa-plane-departure"
      >{{ $t("sidebar.travels") }}</SidebarLink
    >
    <SidebarLink
      :to="Trans.i18nRoute({ name: 'contact' })"
      icon="fa-solid fa-paper-plane"
      >{{ $t("sidebar.contact") }}
    </SidebarLink>
    <LanguageSwitcher></LanguageSwitcher>
    <span
      class="collapse-icon"
      :class="{ 'rotate-180': collapsed }"
      @click="toggleSidebar"
    >
      <i class="fas fa-angle-double-left" />
    </span>
  </div>
</template>

<style>
:root {
  --sidebar-bg-color: #404040;
  --sidebar-bg-image: url("@/assets/sidebarBackground.jpg");
  --sidebar-item-hover: #a4a4a4;
  --sidebar-item-active: #646464;
}
</style>

<style scoped>
.sidebar {
  color: white;
  /*background-color: var(--sidebar-bg-color);*/
  background-image: var(--sidebar-bg-image);
  background-size: cover;

  float: left;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  bottom: 0;
  padding: 0.5em;

  transition: 0.3s ease;

  display: flex;
  flex-direction: column;
}

.sidebar h1 {
  height: 2.5em;
}

.collapse-icon {
  position: absolute;
  bottom: 0;
  padding: 0.75em;

  color: rgba(255, 255, 255, 0.7);

  transition: 0.2s linear;
}

.rotate-180 {
  transform: rotate(180deg);
  transition: 0.2s linear;
}
.logo {
  height: 3em;
}
.logo-collapsed {
  height: 1em;
}
/* Media query for mobile devices */
@media (max-width: 768px) {
  .sidebar {
    position: fixed !important;
    top: auto;
    bottom: 0 !important;
    left: 0 !important;
    width: 100% !important;
    height: 6% !important;
    flex-direction: row !important;
    justify-content: flex-start !important; /* Adjusted to flex-start */
    padding: 1em !important;
    z-index: 1000; /* Ensure it's on top of other content */
    overflow-x: auto; /* Set overflow to visible */
  }

  .sidebar h1 {
    display: none; /* Hide the logo on mobile */
  }

  .sidebar .collapse-icon {
    display: none; /* Hide the collapse icon on mobile */
  }
  /* Adjust styles for links to ensure they display correctly */
  .link {
    flex-grow: 1;
    display: flex;
    align-items: center;
    padding: 0.5em;
    white-space: nowrap; /* Prevent text wrapping */
  }

  .link .icon {
    margin-bottom: 0.5em;
  }
}
</style>

<script>
import SidebarLink from "./SidebarLink";
import LanguageSwitcher from "../LanguageSwitcher.vue";
import { collapsed, toggleSidebar, sidebarWidth } from "./state";
import Trans from "@/Translations";

export default {
  props: {},
  components: { SidebarLink, LanguageSwitcher },
  setup() {
    return { collapsed, toggleSidebar, sidebarWidth, Trans };
  },
};
</script>
