import i18n from "./i18n";
const Trans = {
  get supportedLocales() {
    /*return import.meta.env.VUE_APP_I18N_SUPPORTED_LOCALES.split(",");*/
    return ["en", "ja"];
  },
  set currentLocale(newLocale) {
    i18n.global.locale.value = newLocale;
  },
  get currentLocale() {
    return i18n.global.locale.value;
  },
  get defaultLocale() {
    return import.meta.env.VUE_APP_I18N_LOCALE;
  },
  async switchLanguage(newLocale) {
    Trans.currentLocale = newLocale;
    document.querySelector("html").setAttribute("lang", newLocale);
    window.dispatchEvent(
      new CustomEvent("languageChanged", { detail: { newLocale } })
    );
  },
  isSupportedLocale(locale) {
    return Trans.supportedLocales.includes(locale);
  },
  getUserLocale() {
    const locale =
      window.navigator.language ||
      window.navigator.userLanguage ||
      Trans.defaultLocale;
    return {
      locale: locale,
      localeNoRegion: locale.split("-")[0],
    };
  },
  getPersistedLocale() {
    const persistedLocale = localStorage.getItem("user-locale");
    if (Trans.isSupportedLocale(persistedLocale)) {
      return persistedLocale;
    } else {
      return null;
    }
  },
  guessDefaultLocale() {
    const userPersistedLocale = Trans.getPersistedLocale();
    if (userPersistedLocale) {
      return userPersistedLocale;
    }
    const userPreferredLocale = Trans.getUserLocale();
    if (Trans.isSupportedLocale(userPreferredLocale.locale)) {
      return userPreferredLocale.locale;
    }
    if (Trans.isSupportedLocale(userPreferredLocale.localeNoRegion)) {
      return userPreferredLocale.localeNoRegion;
    }
  },
  async routerMiddleware(to, _from, next) {
    const language = to.params.locale;
    if (!Trans.isSupportedLocale(language)) {
      return next(Trans.guessDefaultLocale());
    }
    await Trans.switchLanguage(language);
    next();
  },
  i18nRoute(to) {
    return {
      ...to,
      params: {
        locale: Trans.currentLocale,
        ...to.params,
      },
    };
  },
};
export default Trans;
