import { RouterView, createRouter, createWebHistory } from "vue-router";
import HomeView from "../views/HomeView.vue";
import Trans from "@/Translations";
import i18n from "@/i18n";

const { t } = i18n.global;
const routes = [
  {
    path: "/:locale?",
    component: RouterView,
    beforeEnter: Trans.routerMiddleware,
    children: [
      {
        path: "",
        name: "home",
        component: HomeView,
        meta: {
          title: "Home Page",
        },
      },
      {
        path: "aboutme",
        name: "aboutme",
        titre: "About Me",
        component: () => import("../views/AboutMeView.vue"),
        meta: {
          title: "About Me",
        },
      },
      {
        path: "programming",
        name: "programming",
        title: "Programming",
        component: () => import("../views/ProgrammingView.vue"),
      },
      {
        path: "art",
        name: "art",
        title: "Art",
        component: () => import("../views/ArtView.vue"),
      },
      {
        path: "music",
        name: "music",
        title: "Music",
        component: () =>
          import(/* webpackChunkName: "about" */ "../views/MusicView.vue"),
      },
      {
        path: "travels",
        name: "travels",
        title: "Travels",
        component: () =>
          import(/* webpackChunkName: "about" */ "../views/TravelsView.vue"),
      },
      {
        path: "contact",
        name: "contact",
        title: "Contact",
        component: () =>
          import(/* webpackChunkName: "about" */ "../views/ContactView.vue"),
      },
    ],
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

router.beforeEach((to, from, next) => {
  window.addEventListener("languageChanged", () => {
    document.title = t("sidebar." + to.name) + " | GHB" || "ERREUR";
  });
  document.title = t("sidebar." + to.name) + " | GHB" || "ERREUR";
  next();
});
export default router;
